import React, { Component } from "react";
import TaskService from '../../../services/task-service';

export default class DeleteTask extends Component {

    state = {
        TaskService: new TaskService(),
    }

    deleteTaskAction = () => {
        const groupLink = this.props.currentGroupLink.link;
        const taskLink = this.props.currentGroupLink.taskLink;

        this.state.TaskService.deleteTaskAction(groupLink, taskLink).then(
            res => {
                console.log(res);
                window.location.reload();
            }
        );
    }

    render() {
        return (
            <div className="confirm">
                <div className="info">Are you sure you want to delete this task?</div>
                <div className="confirm-action">
                    <div className="yes" onClick={() => this.deleteTaskAction()}>Yes</div>
                    <div className="no" onClick={this.props.changePopUpState}>No</div>
                </div>
            </div>
        );
    }
}
